@import "./variaveis.css";
@import "./base.css";

/* COMPONENTS */
@import "./blockquote.css";
@import "./buttons.css";
@import "./inputs.css";
@import "./table.css";
@import "./texts.css";
@import "./labels.css";

.main.shifted > #nav-sidebar + .content {
    max-width: calc(100% - 50px) !important;
}



fieldset.module.aligned h2 {
    /* background: red !important; */
    border-left: 7px solid var(--color-header);
    padding: 11px 20px;
    margin-left: -8px !important;
    color: var(--color-cinza) !important;
    font-family: var(--font-family) !important;
    text-transform: uppercase;
    font-size: 14px !important;
    font-weight: 500 !important;
}




#content-main caption a {
    color: var(--color-cinza) !important;
}
h1#site-name a:hover {
    background: none !important;
}
#site-name img {
    height: 39px !important;
}

body {
    margin: 0;
    padding: 0;
    font-size: 14px;
    /* font-family: "Gotham", "Gotham", "Roboto", "Lucida Grande", "DejaVu Sans", "Bitstream Vera Sans", Verdana, Arial, sans-serif; */
    font-family: var(--font-family) !important;

    color: var(--neutral-100);
    background: var(--color-fundo) !important;
}

/* LINKS */

a:link,
a:visited {
    color: var(--color-cinza);
    text-decoration: none;
}

a:focus {
    text-decoration: underline;
}

a {
    color: var(--neutral-100);
}

a img {
    border: none;
}

a.section {
    /* background: var(--color-strong) !important; */
    padding: 11px 20px;
    margin-left: -8px !important;
    /* color: var(--color-white) !important; */
    color: black !important;
    border-left: 7px solid var(--color-border);
    font-size: 14px !important;
    font-weight: 500;
    /* font-family: verdana; */
}

a.section:link,
a.section:visited {
    color: var(--color-white);
    text-decoration: none;
}

a {
    color: #0c0f0d !important;
}

/* GLOBAL DEFAULTS */

p,
ol,
ul,
dl {
    margin: 0.2em 0 0.8em 0;
}

p {
    padding: 0;
    line-height: 140%;
}

h1,
h2,
h3,
h4,
h5 {
    font-weight: bold;
}

h1 {
    margin: 0 0 20px;
    font-weight: 300;
    font-size: 20px;
}

.content h1 {
    margin: 0 0 20px;
    font-weight: 300;
    font-size: 20px;
    color: var(--color-cinza);
    margin-top: 35px !important;
}

h2 {
    font-size: 16px;
    margin: 1em 0 0.5em 0;
}

h2.subhead {
    font-weight: normal;
    margin-top: 0;
}

h3 {
    font-size: 14px;
    margin: 0.8em 0 0.3em 0;
    color: var(--color-cinza);
    font-weight: bold;
}

h4 {
    font-size: 12px;
    margin: 1em 0 0.8em 0;
    padding-bottom: 3px;
}

h5 {
    font-size: 10px;
    margin: 1.5em 0 0.5em 0;
    color: var(--color-cinza);
    text-transform: uppercase;
    letter-spacing: 1px;
}

ul li {
    list-style-type: square;
    padding: 1px 0;
}

li ul {
    margin-bottom: 0;
}

li,
dt,
dd {
    font-size: 13px;
    line-height: 20px;
}

dt {
    font-weight: bold;
    margin-top: 4px;
}

dd {
    margin-left: 0;
}

form {
    margin: 0;
    padding: 0;
}

fieldset {
    margin: 0;
    padding: 0;
    border: none;
    border-top: 1px solid var(--color-border);
}

code,
pre {
    /* font-family: "Gotham", "Bitstream Vera Sans Mono", Monaco, "Courier New", Courier, monospace; */
    font-family: var(--font-family) !important;

    color: var(--color-cinza);
    font-size: 12px;
}

pre.literal-block {
    margin: 10px;
    background: var(--cor-cinza-claro);
    padding: 6px 8px;
}

code strong {
    color: #930;
}

hr {
    clear: both;
    color: var(--cor-cinza-claro);
    background-color: var(--color-separator);
    height: 1px;
    border: none;
    margin: 0;
    padding: 0;
    font-size: 1px;
    line-height: 1px;
}

.float-right {
    float: right;
}

.float-left {
    float: left;
}

.clear {
    clear: both;
}

.align-left {
    text-align: left;
}

.align-right {
    text-align: right;
}

.example {
    margin: 10px 0;
    padding: 5px 10px;
    background: #efefef;
}

/* FORM DEFAULTS */

input,
textarea,
select,
.form-row p,
form .button {
    margin: 2px 0;
    padding: 2px 3px;
    vertical-align: middle;
    /* font-family: "Gotham", "Roboto", "Lucida Grande", Verdana, Arial, sans-serif; */
    font-family: var(--font-family) !important;

    font-weight: normal;
    font-size: 13px;
}

.form-row div.help {
    padding: 2px 3px;
}

textarea {
    vertical-align: top;
}

input[type="text"],
input[type="password"],
input[type="email"],
input[type="url"],
input[type="number"],
input[type="tel"],
textarea,
select,
.vTextField {
    border: 1px solid var(--cor-cinza-claro);
    border-radius: 4px;
    padding: 5px 6px;
    margin-top: 0;
}

input[type="text"]:focus,
input[type="password"]:focus,
input[type="email"]:focus,
input[type="url"]:focus,
input[type="number"]:focus,
input[type="tel"]:focus,
textarea:focus,
select:focus,
.vTextField:focus {
    border-color: var(--color-cinza);
}

select {
    height: 30px;
}

select[multiple] {
    min-height: 150px;
}

/* FORM BUTTONS */

.button,
input[type="submit"],
input[type="button"],
.submit-row input,
a.button {
    background: var(--color-white);
    padding: 10px 15px;
    border: none;
    border-radius: 4px;
    color: var(--color-white);
    cursor: pointer;
}

a.button {
    padding: 4px 5px;
}

.button:active,
input[type="submit"]:active,
input[type="button"]:active,
.button:focus,
input[type="submit"]:focus,
input[type="button"]:focus,
.button:hover,
input[type="submit"]:hover,
input[type="button"]:hover {
    background: var(--color-strong);
}

.button[disabled],
input[type="submit"][disabled],
input[type="button"][disabled] {
    opacity: 0.4;
}

.button.default,
input[type="submit"].default,
.submit-row input.default {
    float: right;
    border: none;
    font-weight: 400;
    background: var(--color-white);
    color: #0c0f0d !important;
}

.button.default:active,
input[type="submit"].default:active,
.button.default:focus,
input[type="submit"].default:focus,
.button.default:hover,
input[type="submit"].default:hover {
}

.button[disabled].default,
input[type="submit"][disabled].default,
input[type="button"][disabled].default {
    opacity: 0.4;
}

/* MODULES */

.module {
    border: none !important;
    margin-top: 30px !important;
    /* background: #fff !important; */
    /* padding: 30px !important; */
    /* padding-top: 10px !important; */
    /* border-radius: 20px !important; */
    /* box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23); */
    /* padding-left: 50px !important; */
    /* padding-right: 50px !important; */
    /* border: 1px solid var(--color-cinza); */
    margin: 0px;
    border-radius: 0px;
    padding: 25px !important;
    background: var(--color-white);
    -webkit-box-shadow: 0px 9px 24px -14px rgba(0, 0, 0, 0.53) !important;
    -moz-box-shadow: 0px 9px 24px -14px rgba(0, 0, 0, 0.53) !important;
    box-shadow: 0px 9px 24px -14px rgba(0, 0, 0, 0.53) !important;
    border: none;
}

.module tr:hover {
    background: #f7f7fa;
}

a.addlink:hover {
    background: #510c76 !important;
    transition: 0.2s;
    color: white !important;
}

a.changelink:hover {
    background: white !important;
    transition: 0.2s;
    color: #510c76 !important;
}

.module p,
.module ul,
.module h3,
.module h4,
.module dl,
.module pre {
    padding-left: 10px;
    padding-right: 10px;
}

.module blockquote {
    margin-left: 12px;
}

.module ul,
.module ol {
    margin-left: 1.5em;
}

.module h3 {
    margin-top: 0.6em;
}

.module h2,
.module caption,
.inline-group h2 {
    margin: 0;
    padding: 8px;
    font-weight: 400;
    font-size: 13px;
    text-align: left;
    background: var(--color-white);
    color: var(--color-white);
}

.module caption,
.inline-group h2 {
    font-size: 12px;
    letter-spacing: 0.5px;
    text-transform: uppercase;
}

.module table {
    border-collapse: collapse;
}

/* MESSAGES & ERRORS */

ul.messagelist {
    padding: 0;
    margin: 0;
}

ul.messagelist li {
    display: block;
    font-weight: 400;
    font-size: 13px;
    padding: 10px 10px 10px 65px;
    margin: 0 0 10px 0;
    /* background: #dfd url(../img/icon-yes.svg) 40px 12px no-repeat; */
    background-size: 16px auto;
    color: var(--color-cinza);
}

ul.messagelist li.warning {
    /* background: #ffc url(../img/icon-alert.svg) 40px 14px no-repeat; */
    background-size: 14px auto;
}

ul.messagelist li.error {
    /* background: #ffefef url(../img/icon-no.svg) 40px 12px no-repeat; */
    background-size: 16px auto;
}

.errornote {
    font-size: 14px;
    font-weight: 700;
    display: block;
    padding: 10px 12px;
    margin: 0 0 10px 0;
    color: #ba2121;
    border: 1px solid #ba2121;
    border-radius: 4px;
    background-color: var(--color-white);
    background-position: 5px 12px;
}

ul.errorlist {
    margin: 0 0 4px;
    padding: 0;
    color: #ba2121;
    background: var(--color-white);
}

ul.errorlist li {
    font-size: 13px;
    display: block;
    margin-bottom: 4px;
}

ul.errorlist li:first-child {
    margin-top: 0;
}

ul.errorlist li a {
    color: inherit;
    text-decoration: underline;
}

td ul.errorlist {
    margin: 0;
    padding: 0;
}

td ul.errorlist li {
    margin: 0;
}

.form-row.errors {
    margin: 0;
    border: none;
    border-bottom: 1px solid var(--cor-cinza-claro);
    background: none;
}

.form-row.errors ul.errorlist li {
    padding-left: 0;
}

.errors input,
.errors select,
.errors textarea {
    border: 1px solid #ba2121;
}

div.system-message {
    background: #ffc;
    margin: 10px;
    padding: 6px 8px;
    font-size: 0.8em;
}

div.system-message p.system-message-title {
    padding: 4px 5px 4px 25px;
    margin: 0;
    color: #c11;
    /* background: #ffefef url(../img/icon-no.svg) 5px 5px no-repeat; */
}

.description {
    font-size: 12px;
    padding: 5px 0 0 12px;
}

/* BREADCRUMBS */

div.breadcrumbs {
    background: var(--color-white);
    padding: 10px 40px;
    border: none;
    font-size: 14px;
    color: var(--color-cinza);
    text-align: left;
    box-shadow: 0px 1px 24px -14px rgb(0 0 0 / 55%) !important;
}

div.breadcrumbs a {
    color: var(--color-cinza) !important;
}

div.breadcrumbs a:focus,
div.breadcrumbs a:hover {
    color: #c4dce8;
}

/* ACTION ICONS */

.viewlink,
.inlineviewlink {
    padding-left: 16px;
    /* background: url(../img/icon-viewlink.svg) 0 1px no-repeat; */
}

.addlink {
    padding-left: 16px;
    /* background: url(../img/icon-addlink.svg) 0 1px no-repeat; */
}

.changelink,
.inlinechangelink {
    padding-left: 16px;
    /* background: url(../img/icon-changelink.svg) 0 1px no-repeat; */
}

.deletelink {
    padding-left: 16px;
    /* background: url(../img/icon-deletelink.svg) 0 1px no-repeat; */
}

a.deletelink:link,
a.deletelink:visited {
    color: #cc3434;
}

a.deletelink:focus,
a.deletelink:hover {
    color: #993333;
    text-decoration: none;
}

/* OBJECT TOOLS */

.object-tools {
    font-size: 10px;
    font-weight: bold;
    padding-left: 0;
    float: right;
    position: relative;
    margin-top: -48px;
}

.form-row .object-tools {
    margin-top: 5px;
    margin-bottom: 5px;
    float: none;
    height: 2em;
    padding-left: 3.5em;
}

.object-tools li {
    display: block;
    float: left;
    margin-left: 5px;
    height: 16px;
}

.object-tools a {
    border-radius: 15px;
}

.object-tools a:link,
.object-tools a:visited {
    display: block;
    float: left;
    padding: 3px 12px;
    background-color: var(--color-white);
    border: 1px solid var(--color-strong);
    font-weight: 400;
    font-size: 11px;
    text-transform: uppercase;
    letter-spacing: 0.5px;
}

.object-tools a:focus,
.object-tools a:hover {
    background-color: var(--color-white);
    border: 1px solid var(--color-strong);
}

.object-tools a:focus {
    text-decoration: none;
}

.object-tools a.viewsitelink,
.object-tools a.golink,
.object-tools a.addlink {
    background-repeat: no-repeat;
    background-position: right 7px center;
    padding-right: 26px;
}

.object-tools a.viewsitelink,
.object-tools a.golink {
    /* background-image: url(../img/tooltag-arrowright.svg); */
}

.object-tools a.addlink {
    /* background-image: url(../img/tooltag-add.svg); */
}

/* OBJECT HISTORY */

table#change-history {
    width: 100%;
}

table#change-history tbody th {
    width: 16em;
}

/* PAGE STRUCTURE */

#container {
    position: relative;
    width: 100%;
    min-width: 980px;
    padding: 0;
}

#content {
    padding: 20px 40px;
}

.dashboard #content {
    width: 600px;
}

#content-main {
    float: left;
    width: 100%;
}

#content-related {
    float: right;
    width: 260px;
    position: relative;
    margin-right: -300px;
}

#footer {
    clear: both;
    padding: 10px;
}

/* COLUMN TYPES */

.colMS {
    margin-right: 300px;
}

.colSM {
    margin-left: 300px;
}

.colSM #content-related {
    float: left;
    margin-right: 0;
    margin-left: -300px;
}

.colSM #content-main {
    float: right;
}

.popup .colM {
    width: auto;
}

/* HEADER */

#header {
    width: auto;
    height: 40px;
    padding: 10px 40px;
    background: #510c76 !important;
    line-height: 40px;
    color: #0c0f0d !important;
    overflow: hidden;
    -webkit-box-shadow: 0px 2px 5px 0px rgba(0, 0, 0, 0.13);
    -moz-box-shadow: 0px 2px 5px 0px rgba(0, 0, 0, 0.13);
    box-shadow: 0px 2px 5px 0px rgba(0, 0, 0, 0.13);
}

#header a:link,
#header a:visited,
#user-tools a {
    color: var(--color-white) !important;
    padding: 5px;
    margin: 2px;
}

#header a:focus,
#header a:hover {
    transition-duration: 0.1s;
    background: var(--color-adicionar);
    border-radius: 1px;
}

#branding {
    float: left;
}

#branding h1 {
    padding: 0;
    margin: 0 20px 0 0;
    font-weight: 300;
    font-size: 24px;
    color: #f5dd5d;
}

#branding h1,
#branding h1 a:link,
#branding h1 a:visited {
    color: #f5dd5d;
}

#branding h2 {
    padding: 0 10px;
    font-size: 14px;
    margin: -8px 0 8px 0;
    font-weight: normal;
    color: #ffc;
}

#branding a:hover {
    text-decoration: none;
}

#user-tools {
    float: right;
    padding: 0;
    margin: 0 0 0 20px;
    font-weight: 300;
    font-size: 11px;
    letter-spacing: 0.5px;
    text-transform: uppercase;
    text-align: right;
    color: var(--color-white) !important;
}

/* SIDEBAR */

#content-related {
    background: #f8f8f8;
}

#content-related .module {
    background: var(--color-white);
}

#content-related h3 {
    font-size: 14px;
    color: var(--color-cinza);
    padding: 0 16px;
    margin: 0 0 16px;
}

#content-related h4 {
    font-size: 13px;
}

#content-related p {
    padding-left: 16px;
    padding-right: 16px;
}

#content-related .actionlist {
    padding: 0;
    margin: 16px;
}

#content-related .actionlist li {
    line-height: 1.2;
    margin-bottom: 10px;
    padding-left: 18px;
}

#content-related .module h2 {
    background: none;
    padding: 16px;
    margin-bottom: 16px;
    border-bottom: 1px solid var(--cor-cinza-claro);
    font-size: 18px;
    color: var(--color-cinza);
}

.delete-confirmation form input[type="submit"] {
    background: #ba2121;
    border-radius: 4px;
    padding: 10px 15px;
    color: var(--color-white);
}

.delete-confirmation form input[type="submit"]:active,
.delete-confirmation form input[type="submit"]:focus,
.delete-confirmation form input[type="submit"]:hover {
    background: #a41515;
}

.delete-confirmation form .cancel-link {
    display: inline-block;
    vertical-align: middle;
    height: 15px;
    line-height: 15px;
    background: #ddd;
    border-radius: 4px;
    padding: 10px 15px;
    color: var(--color-cinza);
    margin: 0 0 0 10px;
}

.delete-confirmation form .cancel-link:active,
.delete-confirmation form .cancel-link:focus,
.delete-confirmation form .cancel-link:hover {
    background: var(--cor-cinza-claro);
}

/* POPUP */
.popup #content {
    padding: 20px;
}

.popup #container {
    min-width: 0;
}

.popup #header {
    padding: 10px 20px;
}

#content-main a {
    /* font-family: "Gotham Light" !important; */

    font-family: var(--font-family) !important;

    font-size: 14px !important;
    font-weight: 500 !important;
}

.submit-row {
    z-index: 1000 !important;
    padding: 22px 14px;
    margin: 0 0 20px;
    background: var(--cor-cinza-claro);
    border-top: 1px solid var(--cor-cinza-claro);
    /* border: 1px solid var(--cor-cinza-claro); */
    border-radius: 0px;
    text-align: right;
    overflow: hidden;
}

input[type="submit"] {
    background: var(--color-strong);
    /* border: 1px solid var(--neutral-100); */
    color: var(--color-white);
}

.button:active,
input[type="submit"]:active,
input[type="button"]:active,
.button:focus,
input[type="submit"]:focus,
input[type="button"]:focus,
.button:hover,
input[type="submit"]:hover,
input[type="button"]:hover {
    /* background: #609ab6; */
    outline: none !important;
    border: 0px;
}

div#recent-actions-module {
    padding: 4px !important;
}

div#content-related {
    background: none !important;
}

body.dashboard.vsc-initialized strong {
    color: var(--color-white) !important;

    padding: 4px;
    margin: 0px 5px;
}

caption {
    background: none !important;
    margin: 10px 0px !important;
    font-weight: 700 !important;
}

a {
    color: #0c0f0d !important;
}

a.addlink {
    padding: 3px 30px !important;
    margin-top: -4px !important;
    font-weight: 800 !important;
    border-radius: 0px !important;
    /* color: var(--color-white); !important; */
}

.module h2,
.module caption,
.inline-group h2 {
    margin: 0;
    padding: 8px;
    font-weight: 400;
    font-size: 13px;
    text-align: left;
    background: none !important;
    color: var(--color-white);
}

div#user-tools a {
    font-weight: 400;
}

.breadcrumbs a {
    color: var(--color-white) !important;
}

div#user-tools {
    /* color: var(--color-cinza); */
    font-weight: 400 !important;
}

.requiredlabel,
label.required {
    /* font-weight: bold !important; */
    /* text-decoration: underline; */
    color: var(--color-cinza);
    font-weight: 500 !important;
}

div#user-tools strong {
    color: var(--color-white) !important;
}

/* EXTRA STYLES Ã°Å¸â€Â¹Ã°Å¸â€Â¹Ã°Å¸â€Â¹Ã°Å¸â€Â¹Ã°Å¸â€Â¹Ã°Å¸â€Â¹Ã°Å¸â€Â¹Ã°Å¸â€Â¹Ã°Å¸â€Â¹Ã°Å¸â€Â¹Ã°Å¸â€Â¹*/

#content-main table > tbody > tr th {
    vertical-align: middle !important;
}

#content-main table > tbody > tr td {
    vertical-align: middle !important;
}

#content-main table > tbody > tr th:nth-child(1) a {
    padding: 10px 20px;
    width: 90%;
    display: inline-block;
    padding-left: 5px;
}

#content-main table > tbody > tr th:nth-child(1) a:hover {
    text-decoration: underline !important;
}

#content-main table > tbody > tr td:nth-child(3) a {
    font-weight: 500 !important;
    background: var(--color-btn-secundary);
    padding: 8px 20px;
}

a.addlink {
    background: var(--color-adicionar) !important;
    color: var(--color-btn-adicionar) !important;
    padding: 8px 20px !important;
    border-radius: 4px !important;
    margin: 0px 0 !important;
}

td,
th {
    padding: 7px;
}

form .tabular.inline-related.last-related {
    max-width: 100% !important;
    overflow-x: scroll;
}

#realizadoano_set-0 .original p {
    display: none;
}

/* DASHBOARD */

.dashboard .module table th {
    width: 100%;
}

.dashboard .module table td {
    white-space: nowrap;
}

.dashboard .module table td a {
    display: block;
    padding-right: 0.6em;
}

/* RECENT ACTIONS MODULE */

.module ul.actionlist {
    margin-left: 0;
}

ul.actionlist li {
    list-style-type: none;
    overflow: hidden;
    text-overflow: ellipsis;
}

.sticky {
    position: sticky;
    top: 0;
    max-height: 100vh;
}

.toggle-nav-sidebar {
    z-index: 20;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    flex: 0 0 23px;
    width: 23px;
    border: 0;
    border-right: 1px solid var(--hairline-color);
    background-color: var(--body-bg);
    cursor: pointer;
    font-size: 20px;
    color: var(--link-fg);
    padding: 0;
}

[dir="rtl"] .toggle-nav-sidebar {
    border-left: 1px solid var(--hairline-color);
    border-right: 0;
}

.toggle-nav-sidebar:hover,
.toggle-nav-sidebar:focus {
    background-color: var(--darkened-bg);
}

#nav-sidebar {
    z-index: 15;
    flex: 0 0 275px;
    left: -276px;
    margin-left: -276px;
    border-top: 1px solid transparent;
    border-right: 1px solid var(--hairline-color);
    background-color: var(--body-bg);
    overflow: auto;
}

[dir="rtl"] #nav-sidebar {
    border-left: 1px solid var(--hairline-color);
    border-right: 0;
    left: 0;
    margin-left: 0;
    right: -276px;
    margin-right: -276px;
}

.toggle-nav-sidebar::before {
    content: "\00BB";
}

.main.shifted .toggle-nav-sidebar::before {
    content: "\00AB";
}

.main.shifted > #nav-sidebar {
    left: 24px;
    margin-left: 0;
}

[dir="rtl"] .main.shifted > #nav-sidebar {
    left: 0;
    right: 24px;
    margin-right: 0;
}

#nav-sidebar .module th {
    width: 100%;
    overflow-wrap: anywhere;
}

#nav-sidebar .module th,
#nav-sidebar .module caption {
    padding-left: 16px;
}

#nav-sidebar .module td {
    white-space: nowrap;
}

[dir="rtl"] #nav-sidebar .module th,
[dir="rtl"] #nav-sidebar .module caption {
    padding-left: 8px;
    padding-right: 16px;
}

#nav-sidebar .current-app .section:link,
#nav-sidebar .current-app .section:visited {
    color: var(--header-color);
    font-weight: bold;
}

#nav-sidebar .current-model {
    background: var(--selected-row);
}

.main > #nav-sidebar + .content {
    max-width: calc(100% - 23px);
}

.main.shifted > #nav-sidebar + .content {
    max-width: calc(100% - 299px);
}

@media (max-width: 767px) {
    #nav-sidebar,
    #toggle-nav-sidebar {
        display: none;
    }

    .main > #nav-sidebar + .content,
    .main.shifted > #nav-sidebar + .content {
        max-width: 100%;
    }
}
